</template>

<script>
import snow from '@/components/swiper/snow.vue';

export default {
  components: {
    snow,
  },
  data() {
    return {
      currentSection: 0,
      sections: [
        { name: "One Cleaner 소개", ref: 'section1' },
        { name: "아파트 청소", ref: 'section2' },
        { name: "빌라 청소", ref: 'section3' },
        { name: "원룸 청소", ref: 'section4' },
        { name: "병원 청소", ref: 'section5' }
      ],
      sectionElements: [],
      isScrolling: false,
      scrollTimeout: null,
      touchStartY: 0,
      touchEndY: 0,
      hoveredSection: null,
    };
  },
  mounted() {
    // 각 section의 DOM 요소를 sectionElements 배열에 저장
    this.sectionElements = this.sections.map(section => this.$refs[section.ref]);
    
    window.addEventListener("wheel", this.onWheel);
    window.addEventListener("touchstart", this.onTouchStart);
    window.addEventListener("touchmove", this.onTouchMove);
    window.addEventListener("touchend", this.onTouchEnd);
  },
  beforeDestroy() {
    window.removeEventListener("wheel", this.onWheel);
    window.removeEventListener("touchstart", this.onTouchStart);
    window.removeEventListener("touchmove", this.onTouchMove);
    window.removeEventListener("touchend", this.onTouchEnd);
  },
  methods: {
    onWheel(event) {
      if (this.isScrolling) return;
      this.isScrolling = true;
      if (event.deltaY > 0) {
        this.nextSection();
      } else {
        this.prevSection();
      }
      clearTimeout(this.scrollTimeout);
      this.scrollTimeout = setTimeout(() => {
        this.isScrolling = false;
      }, 1000);
    },
    onTouchStart(event) {
      this.touchStartY = event.touches[0].clientY;
    },
    onTouchMove(event) {
      this.touchEndY = event.touches[0].clientY;
    },
    onTouchEnd() {
      if (this.isScrolling) return;
      this.isScrolling = true;
      if (this.touchEndY - this.touchStartY > 50) {
        this.prevSection();
      } else if (this.touchStartY - this.touchEndY > 50) {
        this.nextSection();
      }
      clearTimeout(this.scrollTimeout);
      this.scrollTimeout = setTimeout(() => {
        this.isScrolling = false;
      }, 1000);
    },
    nextSection() {
      if (this.currentSection < this.sections.length - 1) {
        this.currentSection++;
        this.scrollToSection(this.currentSection);
      }
    },
    prevSection() {
      if (this.currentSection > 0) {
        this.currentSection--;
        this.scrollToSection(this.currentSection);
      }
    },
    scrollToSection(index) {
      this.currentSection = index;
      const section = this.sectionElements[this.currentSection];
      section.scrollIntoView({ behavior: "smooth" });
    },
    hoverSection(index) {
      this.hoveredSection = index;
    },
    leaveSection() {
      this.hoveredSection = null;
    },
  },
};
</script>

